import React, {Component} from "react";
import PropTypes from "prop-types";
import IconChevron from '../../icons/IconChevron';

import st from "./style";

class ExpandCollapseContainer extends Component{
	
	constructor(props) {
		super(props);
		this.state = {
			active: false
		};
	}

	onClick = () => {
		const value = !this.state.active;
		this.setState({
			active:value
		});
	}

	render(){
		return <div style={{...st.container,...this.props.style}}>
			<div onClick={this.onClick} style={st.ctnHeader}>
				<div style={st.label}> {this.props.label}</div>
				{this.props.showArrow?
					<IconChevron direction={this.state.active?'top':'bottom'}/>
					:null
				}
			</div>
			<div style={st.ctnChildren}>
				{
					this.state.active?
					this.props.children
					:null
				}
			</div>
		</div>
	}
}

ExpandCollapseContainer.propTypes = {
	label:PropTypes.string,
	onClick:PropTypes.func,
	showArrow:PropTypes.bool,
	style: PropTypes.object,
	children: PropTypes.oneOfType([
    PropTypes.element,
    PropTypes.node,
    PropTypes.array,
	]),
};

ExpandCollapseContainer.defaultProps = {
	label:"Group Item",
	onClick:()=>{},
	showArrow:true,
	style: {},
	children: null,
};

export default ExpandCollapseContainer;